import React,{useState} from 'react'
import {Paper, Button, Dialog, DialogContent, DialogActions, DialogTitle, Typography} from '@mui/material';
import CarShareTable from './CarShareTable';

const Alarm = () => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  }
  const handleClose = () => {
    setOpen(false)
  }
  return (
    <Paper style={{margin:"auto"}}>
      <Button vairant="contained" color="success" onClick={handleOpen}>  :       . </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>
            
        <Typography>      .</Typography>
        </DialogTitle>
        <DialogContent>
          <CarShareTable/>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" color="success" onClick={handleClose}>5  </Button>
          <Button variant="outlined" color="success" onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </Paper>
  )
}

export default Alarm