import {Radio, RadioGroup, FormControlLabel, FormControl, FormLabel, Container, TextField, Stack} from '@mui/material';
import axios from 'axios'
import {useSelector} from'react-redux';
import {useState} from 'react';
import ValidateInput from './ValidateInput';
import regex from './regex';

export default ({ inputUserInfo, setInputUserInfo }) => {
  const userInfo = useSelector(state=>state.user.userInfo);
  const addCustomer = () => {
    const url = "/lifeConcierge/api/signUp";
    const data = {
      email:inputUserInfo.email,
      pw:inputUserInfo.pw,
      name:inputUserInfo.name,
      gender:inputUserInfo.gender,
      birthday:inputUserInfo.birthday,
      job:inputUserInfo.job,
      hAddr:inputUserInfo.hAddr,
      cAddr:inputUserInfo.cAddr,
      disease:inputUserInfo.disease
    }
    return axios.post(url,data);
  }
  const handleFormSubmit = (e) => {
    e.preventDefault()
    addCustomer()
    .then((res)=>{console.log(res)})
    .catch((err)=>{console.log(err)});
  }
  const [checkId, setCheckId] = useState({
    error:false,
    helperText:""
  })
  const handleOnChange = (e)=>{
    setInputUserInfo({...inputUserInfo, email:e.target.value})
    
  }
  return(
    <Container sx={{border:"1px solid black", padding:"20px", marginTop:"2%"}}>
      <form onSubmit={handleFormSubmit}>
        <Stack spacing={2} alignItems="stretch">
          <ValidateInput label="" type="text" form="email" value={inputUserInfo.email} setValue={setInputUserInfo} maxValue={10} regexCheck={regex.email} defaultText="  ." successText="" errorText="    ."/>
          {/* <TextField defaultValue=" ." error={inputUserInfo.email===""?true:false} helperText={inputUserInfo.email===""?" .  .":""} label="E-mail" name="email" value={inputUserInfo.email} onChange={handleOnChange}></TextField> */}
          <ValidateInput label="" type="password" form="pw" value={inputUserInfo.pw} setValue={setInputUserInfo} maxValue={10} regexCheck={regex.password} defaultText="  ." successText="" errorText="++(_,-)+(6 , 10 )"/>
          {/* <TextField error helperText="8      ." label="" type="password" name="pw" onChange={(e)=>{setInputUserInfo({...inputUserInfo, pw:e.target.value})}}></TextField> */}
          <TextField label="" name="name" value={inputUserInfo.name} onChange={(e)=>{setInputUserInfo({...inputUserInfo, name:e.target.value})}}></TextField>
          <FormControl>
            <FormLabel id="demo-radio-buttons-group-label"></FormLabel>
            <RadioGroup
              row
              aria-labelledby="demo-radio-buttons-group-label"
              name="gender"
            >
              <FormControlLabel value="" control={<Radio onChange={(e)=>{setInputUserInfo({...inputUserInfo, gender : e.target.value})}}/>} label="" />
              <FormControlLabel value="" control={<Radio onChange={(e)=>{setInputUserInfo({...inputUserInfo, gender : e.target.value})}}/>} label="" />
            </RadioGroup>
          </FormControl>
          <TextField required helperText="" type="date" name="birthday" value={inputUserInfo.birthday} onChange={(e)=>{setInputUserInfo({...inputUserInfo, birthday:e.target.value})}}></TextField>
          <TextField label="" name="job" value={inputUserInfo.job} onChange={(e)=>{setInputUserInfo({...inputUserInfo, job:e.target.value})}}></TextField>
          <TextField label="" name="hAddr" value={inputUserInfo.hAddr} onChange={(e)=>{setInputUserInfo({...inputUserInfo, hAddr:e.target.value})}}></TextField>
          <TextField label="" name="cAddr" value={inputUserInfo.cAddr} onChange={(e)=>{setInputUserInfo({...inputUserInfo, cAddr:e.target.value})}}></TextField>
          <TextField label="" name="disease" value={inputUserInfo.disease} onChange={(e)=>{setInputUserInfo({...inputUserInfo, disease:e.target.value})}}></TextField>
        </Stack>
      </form>
    </Container>
  );
}